.page {
  font-size: 16px;
  scroll-behavior: smooth;

  &__menu{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target{
      opacity: 1;
      transform: translate(0);
      pointer-events: all;
   }
  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  ::-webkit-scrollbar {
    display: none;
  }
}
