.nav{
  &__list{
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link{
    position: relative;
    color: $white-color;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

  &::after{
    content: '';
    position: absolute;
    bottom: -8px;
    display: block;
    height: 1px;
    width: 100%;
    background-color: $white-color;

    transform: scale(0);
    transition: transform 0.3s;
    transform-origin: left;
  }

  &:hover::after{
    transform: scale(1);
  }
 }
}
